html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6,
span,
div,
p,
small {
  font-family: 'Roboto', sans-serif;
}
.block {
  box-sizing: border-box;
  overflow: hidden;
  padding: 1rem;
  width: 100%;
}
.block__content {
  word-wrap: break-word;
  overflow: hidden;
  line-height: 1.5rem;
  font-weight: 300;
}
.block__content--center {
  text-align: center;
}
.block:after {
  display: block;
  content: "";
  height: 0;
  visibility: hidden;
}
.block--dark {
  background-color: rgba(255,255,255,0.2);
}
.block--dark__content {
  color: #fffbf2;
}
.block--dark-highlight {
  background-color: #e69900;
}
.block--dark-highlight__content {
  color: #fffbf2;
}
.block--light {
  background-color: rgba(0,0,0,0.2);
}
.block--light__content {
  color: #0d0900;
}
.block--light-highlight {
  background-color: #ffb31a;
}
.block--light-highlight__content {
  color: #0d0900;
}
.button {
  transition: all 100ms ease-in-out;
  display: block;
  padding: 1rem 1rem;
  text-align: center;
  font-size: 1.5rem;
}
.button:hover {
  transition: all 100ms ease-in-out;
}
.button--primary {
  background-color: #fa0;
  color: #fffbf2;
  border: 1px solid rgba(0,0,0,0.2);
  text-decoration: none;
}
.button--primary:hover {
  background-color: #fffbf2;
  color: #fa0;
  border-color: #fa0;
}
.button-- {
  width: 100%;
}
.card {
  overflow: hidden;
  padding: 1rem;
  width: 100%;
}
.card__header {
  padding: 1rem;
}
.card__title {
  font-weight: 600;
  font-size: 2rem;
}
.card__body {
  padding: 1rem 0;
}
.card__list {
  list-style: none;
}
.card__list-item {
  list-style: none;
  padding: 1rem;
}
.card__list-item-title {
  font-size: 1.5rem;
}
.card__list-item-content {
  margin-top: 0.5rem;
}
.card--light {
  background-color: rgba(255,255,255,0.2);
  border: 1px solid rgba(0,0,0,0.2);
}
.card--light__header {
  border-bottom: 1px solid #ffb31a;
}
.card--light__title {
  color: #0d0900;
}
.card--light__list-item-title {
  color: #0d0900;
}
.card--dark {
  background-color: rgba(0,0,0,0.2);
  border: 1px solid rgba(255,255,255,0.2);
}
.card--dark__header {
  border-bottom: 1px solid #e69900;
}
.card--dark__title {
  color: #fffbf2;
}
.card--dark__list-item-title {
  color: #fffbf2;
}
.code {
  font-family: Consolas, monospace;
  padding: 0.5rem;
  word-wrap: break-word;
  white-space: pre;
}
.code--light {
  background-color: rgba(0,0,0,0.2);
  border: 1px solid rgba(0,0,0,0.2);
  color: #0d0900;
}
.code--dark {
  background-color: rgba(255,255,255,0.2);
  border: 1px solid rgba(255,255,255,0.2);
  color: #fffbf2;
}
.footer {
  padding: 1rem 0;
}
.footer--light {
  background-color: #fffbf2;
}
.footer--dark {
  background-color: #0d0900;
}
.jumbotron {
  padding: 4rem 0;
}
.jumbotron__header {
  padding: 2rem 0;
}
.jumbotron__title {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 5rem;
  font-weight: 700;
}
.jumbotron__subtitle {
  font-weight: 100;
  font-size: 2.5rem;
  display: block;
  text-align: center;
}
.jumbotron__underline {
  width: 200px;
  height: 2px;
